<template>
  <div>
    <el-form :model="conditionDict" :inline="true" class="demo-form-inline">
      <el-form-item label="业务id:">
        <el-input
          v-model="conditionDict.id"
          size="mini"
          placeholder="请输入业务id"
          clearable
        />
      </el-form-item>
      <el-form-item label="业务名称:">
        <el-input
          v-model="conditionDict.business_name"
          size="mini"
          placeholder="输入业务名称"
          clearable
        />
      </el-form-item>
      <el-form-item label="所属项目:">
        <el-select
          v-model="conditionDict.project_id"
          clearable
          placeholder="请选择"
          filterable
          size="mini"
        >
          <el-option
            v-for="item in projectList"
            :key="item.label"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          round
          size="mini"
          @click="getListByCondition('search')"
          >查询
        </el-button>
        <el-button type="success" round size="mini" @click="openAddDialog"
          >新增
        </el-button>
        <el-button type="info" round size="mini" @click="reset"
          >重置
        </el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData"
      :row-class-name="tableRowClassName"
      :header-cell-style="getHeaderClass"
      border
      style="width: 100%"
      size="mini"
    >
      <el-table-column
        fixed
        prop="id"
        label="id"
        align="center"
        show-overflow-tooltip
      />
      <el-table-column
        fixed
        prop="business_name"
        label="名称"
        align="center"
        show-overflow-tooltip
      />
      <el-table-column
        fixed
        prop="project"
        label="所属项目"
        align="center"
        show-overflow-tooltip
      />
      <el-table-column
        prop="add_time"
        label="创建时间"
        align="center"
        show-overflow-tooltip
      />
      <el-table-column
        prop="update_time"
        label="更新时间"
        align="center"
        show-overflow-tooltip
      />
      <el-table-column fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="getBusiness(scope.row.id)"
            >编辑
          </el-button>

          <el-button
            type="text"
            size="small"
            @click="deleteBusiness(scope.row.id)"
            >删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :current-page.sync="currentPage"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      :total="total"
      layout="total, sizes, prev, pager, next, jumper"
      style="margin-top: 10px"
      @size-change="handleSizeChange"
      @current-change="getListByCondition"
    />
    <el-dialog
      :visible.sync="addBusinessDialog"
      :before-close="closeAddDialog"
      title="新增"
      width="70%"
    >
      <el-card>
        <el-form ref="addForm" :model="addForm" :rules="rules" :inline="true">
          <el-form-item label="业务名称" prop="business_name">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              v-model="addForm.business_name"
              placeholder="请输入业务名称"
              size="mini"
              clearable
              @change="$forceUpdate()"
              style="width:350px"
            />
          </el-form-item>
          <el-form-item label="所属项目:" prop="project_id">
            <el-select
              v-model="addForm.project_id"
              clearable
              placeholder="请选择"
              filterable
              size="mini"
              @change="$forceUpdate()"
              style="width:350px"
            >
              <el-option
                v-for="item in addProjectList"
                :key="item.label"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-card>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="closeAddDialog">取 消</el-button>
        <el-button size="mini" type="primary" @click="addBusiness('addForm')"
          >保 存
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      :visible.sync="editBusinessDialog"
      :before-close="closeEditDialog"
      title="编辑"
      width="70%"
    >
      <el-card>
        <el-form
          ref="editForm"
          :model="editForm"
          :rules="editRules"
          :inline="true"
        >
          <el-form-item label="业务名称" prop="business_name">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4 }"
              v-model="editForm.business_name"
              placeholder="请输入业务名称"
              size="mini"
              clearable
              @change="$forceUpdate()"
              style="width:350px"
            />
          </el-form-item>
          <el-form-item label="所属项目:" prop="project_id">
            <el-select
              v-model="editForm.project_id"
              clearable
              placeholder="请选择"
              filterable
              size="mini"
              @change="$forceUpdate()"
              style="width:350px"
            >
              <el-option
                v-for="item in editProjectList"
                :key="item.label"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-card>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="closeEditDialog">取 消</el-button>
        <el-button size="mini" type="primary" @click="editBusiness('editForm')"
          >保 存
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'BusinessConfig',
  data() {
    return {
      editForm: {},
      addForm: {},
      addBusinessDialog: false,
      editBusinessDialog: false,
      currentPage: 1,
      projectList: [],
      addProjectList: [],
      editProjectList: [],
      conditionDict: { id: '', business_name: '', project_id: '' },
      pageSize: 10,
      pageSizes: [5, 10, 20],
      total: 0,
      tableData: [],
      rules: {
        business_name: [{ required: true, message: '业务名称不能为空' }],
        project_id: [{ required: true, message: '所属项目不能为空' }]
      },
      editRules: {
        business_name: [{ required: true, message: '业务名称不能为空' }],
        project_id: [{ required: true, message: '所属项目不能为空' }]
      }
    };
  },
  // beforeMount() {
  //   this.getListByCondition();
  // },
  methods: {
    // 打开新增弹窗
    openAddDialog() {
      this.addBusinessDialog = true;
      this.getConditions('add');
    },
    // 获取项目下拉查询条件
    getConditions(type) {
      let url = String.format(
        '{0}/projectInterface/projectListConditions',
        this.GLOBAL.MONITOR_URL
      );
      this.axios
        .get(url)
        .then(response => {
          if (response.data.code === '200') {
            if (type === 'list') {
              this.projectList = response.data.project_list;
            } else if (type === 'add') {
              this.addProjectList = response.data.project_list;
            } else if (type === 'edit') {
              this.editProjectList = response.data.project_list;
            }
          } else {
            this.$message.error(response.data.msg);
          }

          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // 重置搜索
    reset() {
      this.conditionDict = { id: '', business_name: '', project_id: '' };
      this.pageSize = 10;
      this.pageSizes = [5, 10, 20];
      this.currentPage = 1;
      this.getConditions('list');
      this.getListByCondition('search');
    },
    // 关闭项目新增弹窗
    closeAddDialog() {
      this.addBusinessDialog = false;
      this.addProjectList = [];
      this.$refs['addForm'].resetFields();
    },
    // 关闭项目编辑弹窗
    closeEditDialog() {
      this.editBusinessDialog = false;
      this.editProjectList = [];
      this.$refs['editForm'].resetFields();
    },
    // 新增项目
    addBusiness(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let url = String.format(
            '{0}/businessInterface/addBusiness',
            this.GLOBAL.MONITOR_URL
          );
          // 指定参数格式
          let config = {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          };
          this.axios
            .post(url, JSON.stringify(this.addForm), config)
            .then(response => {
              if (response.data.code === '200') {
                this.closeAddDialog();
                this.$message.success('新增成功！');
                this.getListByCondition('search');
              } else {
                this.$message.error(response.data.msg);
              }
              return response;
            })
            .catch(response => {
              return response;
            });
        }
      });
    },
    // 根据业务id获取业务
    getBusiness(businessId) {
      this.editBusinessDialog = true;
      this.getConditions('edit');
      let url = String.format(
        '{0}/businessInterface/businessInfo',
        this.GLOBAL.MONITOR_URL
      );
      // 指定参数格式
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let params = {
        id: businessId
      };
      this.axios
        .post(url, JSON.stringify(params), config)
        .then(response => {
          if (response.data.code === '200') {
            this.editForm = response.data.business_info;
          } else {
            this.editForm = response.data.business_info;
            this.$message.error(response.data.msg);
          }
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // 根据业务id删除项目
    async deleteBusiness(businessId) {
      const result = await this.$confirm(
        '此操作将永久删除该业务, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err);
      if (result === 'confirm') {
        let url = String.format(
          '{0}/businessInterface/removeBusiness',
          this.GLOBAL.MONITOR_URL
        );
        // 指定参数格式
        let config = {
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          }
        };
        let params = {
          id: businessId
        };
        this.axios
          .post(url, JSON.stringify(params), config)
          .then(response => {
            if (response.data.code === '200') {
              this.$message.success('删除成功！');
              this.getListByCondition('search');
            } else {
              this.$message.error(response.data.msg);
            }
            return response;
          })
          .catch(response => {
            return response;
          });
      }
    },
    // 编辑业务
    editBusiness(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let url = String.format(
            '{0}/businessInterface/editBusiness',
            this.GLOBAL.MONITOR_URL
          );
          // 指定参数格式
          let config = {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          };
          this.axios
            .post(url, JSON.stringify(this.editForm), config)
            .then(response => {
              if (response.data.code === '200') {
                this.closeEditDialog();
                this.$message.success('更新成功！');
                this.getListByCondition('search');
              } else {
                this.$message.error(response.data.msg);
              }
              return response;
            })
            .catch(response => {
              return response;
            });
        }
      });
    },
    // 获取列表
    getListByCondition(type) {
      if (type === 'search') {
        this.currentPage = 1;
      }
      let url = String.format(
        '{0}/businessInterface/searchBusiness',
        this.GLOBAL.MONITOR_URL
      );
      // 指定参数格式
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      this.conditionDict['pageSize'] = this.pageSize;
      this.conditionDict['page_index'] = this.currentPage;
      this.axios
        .post(url, JSON.stringify(this.conditionDict), config)
        .then(response => {
          if (response.data.code === '200') {
            this.tableData = response.data.businessInfo.tableData;
            this.total = response.data.total;
          } else {
            this.tableData = response.data.businessInfo.tableData;
            this.total = response.data.total;
            this.$message.error(response.data.msg);
          }

          return response;
        })
        .catch(response => {
          return response;
        });
    },
    // 当每页的条数改变时
    handleSizeChange(val) {
      this.pageSize = val;
      this.currentPage = 1;
      this.getListByCondition();
    },
    // 设置行的颜色
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'warning-row';
      } else {
        return 'success-row';
      }
    },
    // 控制表格头部颜色
    getHeaderClass({ rowIndex }) {
      if (rowIndex === 0) {
        return 'background:deepskyblue';
      } else {
        return '';
      }
    }
  }
};
</script>

<style scoped>
body .el-table th.gutter {
  display: table-cell !important;
}

body .el-table colgroup.gutter {
  display: table-cell !important;
}

.el-table >>> .warning-row {
  background: oldlace;
}

.el-table >>> .success-row {
  background: #f0f9eb;
}

td:not(.is-hidden):last-child {
  right: -1px;
}

.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 40px;
}

.el-row {
  line-height: 40px;
  margin-top: 10px;
}
</style>



